import React, { Component } from 'react'
import Todoinput from './Todoinput'
import Todoing from './Todoing'
export default class Todolist extends Component {
    constructor(){
        super();
         if(localStorage.length === 0){
        this.state = {
            list1:[],
            list2:[]
        }
    }else if(localStorage.length === 1){
        this.state = {
            list1:JSON.parse(localStorage.getItem('key1')),
            list2:[]
        }
    }else if(localStorage.length === 2){
        this.state = {
            list1:JSON.parse(localStorage.getItem('key1')),
            list2:JSON.parse(localStorage.getItem('key2'))
        }
    }else{
        this.state = {
            list1:[],
            list2:[]
        }
    } 
    }
    addItem = (value) => {
            let list = this.state.list1;
            list.push(value);
            this.setState({
                list1 : list
            },() => localStorage.setItem('key1',JSON.stringify(list)))
    }
    deleteItem1 = (idx) => {
        let list = this.state.list1;
        list.splice(idx,1);
        this.setState({
            list1 : list
        },() => localStorage.setItem('key1',JSON.stringify(list)))
    }
    deleteItem2 = (idx) => {
        let list = [...this.state.list2];
        list.splice(idx,1);
        this.setState({
            list2 : list
        },() => localStorage.setItem('key2',JSON.stringify(list)))
    }
    submitItem = (idx) => {
        let list = [...this.state.list1];
        let llist = [...this.state.list2];
        llist.push(list[idx]);
        list.splice(idx,1);
        this.setState({
            list1 : list,
            list2 : llist
        },() => {localStorage.setItem('key1',JSON.stringify(list));localStorage.setItem('key2',JSON.stringify(llist))})}
    returnItem = (idx) => {
        let list = [...this.state.list1];
        let llist = [...this.state.list2];
       list.push(llist[idx]);
        llist.splice(idx,1);
        this.setState({
            list1 : list,
            list2 : llist
        },() => {localStorage.setItem('key1',JSON.stringify(list));localStorage.setItem('key2',JSON.stringify(llist))})
    }
    render() {
        return (
            <div>
               <Todoinput addtodo = {this.addItem}/>
               <Todoing todo = {this.deleteItem1} tosubmit = {this.submitItem} todelete = {this.deleteItem2} todolist1 = {this.state.list1} todolist2 = {this.state.list2} toreturns = {this.returnItem}/>
            </div> 
            
        )
    }
}
